.scatter-page {
  margin-top: 30px;

  .dropdown {
    margin-bottom: 20px;

    .btn {
      width: 80%;
      margin-left: 10%;
    }
    .dropdown-menu {
      width: 90%;
      margin-left: 5%;
      text-align: center;
      li {
        cursor: pointer;
      }
    }
  }

  .configuration-area {
    background-color: #f2f2f2;
    height: 348px;
    overflow: auto;
    border: 1px solid rgba(128, 128, 128, 0.5);

    .pure-random {
      margin-top: 40px;
    }
    .shooting {
      margin-top: 40px;

      .row {
        width: 80%;
        margin-left: 10%;
      }
      .bins-input {
        width: 70%;
        margin-left: 15%;
      }
      .shot-container {
        text-align: center;
      }
      .shot {
        width: 100%;
        margin-top: 20px;
      }
    }
    .configurable-random {
      margin-top: 30px;

      .config-option {
        width: 60%;
        margin-left: 20%;
      }
    }
    .apply-button {
      position: absolute;
      bottom: 30px;
      left: 0;
    }
    .col-md-4 {
      padding: 0;
    }
    .row {
      margin: 0;
    }
    .modified {
      width: 60px;
      display: inline-block;
    }
    .position-dynamic {
      position: unset;
      margin-bottom: 30px;
      margin-top: 30px;
    }
  }

  /* Customize pure-random container */
  @media (min-width: 0px) {
    .pure-random {
      margin-left: 33%;
    }
    .apply-button {
      width: 40%;
      margin-left: 30%;
    }
    .shooting {
      .special-small {
        float: left;
        width: 50%;
        text-align: center;
      }
    }
    .chart-area {
      border: none;
    }
  }

  @media (min-width: 768px) {
    .pure-random {
      margin-left: 40px;
    }
    .apply-button {
      width: 80%;
      margin-left: 10%;
    }
    .configuration-area {
      margin-bottom: 0;
    }
    .shooting {
      .special-small {
        float: none;
        width: 100%;
        text-align: center;
      }
    }
    .chart-area {
      border: 1px solid rgba(128, 128, 128, 0.5);
    }
  }

  @media (min-width: 992px) {
    .pure-random {
      margin-left: 80px;
    }
    .shooting {
      .special-small {
        float: left;
        width: 50%;
        text-align: center;
      }
    }
    .apply-button {
      width: 60%;
      margin-left: 20%;
    }
  }

  @media (min-width: 1200px) {
    .pure-random {
      margin-left: 110px;
    }
    .shooting {
      .special-small {
        float: left;
        width: 50%;
        text-align: center;
      }
    }
    .apply-button {
      width: 40%;
      margin-left: 30%;
    }
  }

  h3 {
    text-align: center;
  }
}
