footer {
  margin-top: 50px;
}

footer::before {
  content: '';
  display: block;
  width: 90%;
  margin-bottom: 20px;
  border-top: 1px solid #dedede;
  margin-left: auto;
  margin-right: auto;
}

footer p {
  float: left;
  color: #999;
  letter-spacing: 0.3px;
  font-size: 14px;
  padding-bottom: 20px;
  margin-left: 10%;
}

footer .social-media-icons {
  float: right;
  margin-right: 10%;
}

footer .social-media-icons i {
  font-size: 24px;
  color: #999;
  transition: 0.5s ease-in-out;
}

footer .social-media-icons a {
  margin-right: 20px;
}

footer .footer-github:hover i {
  color: #000000;
}

footer .footer-facebook:hover i {
  color: #3b5998;
}

footer .footer-vk:hover i {
  color: #507299;
}

footer .footer-linkedin:hover i {
  color: #0077b5;
}
